@font-face {
  font-family: main-font;
  src: url("Orbitron-ExtraBold.c67f8c93.ttf");
}

@font-face {
  font-family: code-font;
  src: url("RobotoMono-Bold.05c1ae7c.ttf");
}

canvas {
  position: fixed;
  top: 0;
  left: 0;
}

main {
  position: absolute;
}

a {
  color: #fff;
  text-decoration: none;
}

a:visited, a:active {
  color: #fff;
}

.progress-bar-container {
  background-color: #306b6d;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-family: main-font;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#progress-bar {
  width: 30%;
  height: 2%;
  margin-top: .5%;
}

label {
  color: #fff;
  font-size: 2rem;
}

.nav-panel {
  flex-direction: column;
  align-items: center;
  display: none;
  position: fixed;
  right: 2vw;
}

.nav-panel button:hover {
  color: #68f8d9;
  text-shadow: 1px 1px 10px #68f8d9, 1px 1px 10px #fff;
  cursor: pointer;
}

.nav-panel button {
  color: #fff;
  background: none;
  border: none;
  margin-top: 20px;
  font-family: main-font;
  font-size: 1.5rem;
}

.section {
  color: #fff;
  background-color: #000c;
  border: .001px groove #030303;
  border-radius: 3px;
  flex-direction: column;
  width: 400px;
  height: 300px;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.section-title {
  color: #fff;
  background-color: #577f8b;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  width: 100%;
  margin-top: 0;
  font-size: 13px;
}

.section-title p {
  margin-left: 10px;
}

.section-content {
  flex-direction: column;
  display: flex;
}

.section-content p {
  margin-left: 20px;
}

.section-content a :hover {
  color: #68f8d9;
}

.section * {
  font-family: code-font;
}

@media (width <= 500px) {
  .nav-panel {
    flex-direction: row;
    justify-content: center;
    width: 100%;
    bottom: 2vh;
  }

  .section {
    width: 300px;
    height: 400px;
    font-size: 12px;
  }

  .nav-panel button {
    font-size: 1rem;
  }
}

